import { Component, EventEmitter, Input, Output } from '@angular/core';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { APP_ROUTE } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { AgentProfileAction, AgentStatus } from '@core/enums/agent.enum';
import { Agents } from '@core/models';
import { environment as env } from '@environments/environment';
import { BottomsheetComponent } from '@feature/meeting-schedule/components/bottomsheet/bottomsheet.component';

@Component({
    selector: 'findqo-agent-profile',
    templateUrl: './agent-profile.component.html',
    styleUrl: './agent-profile.component.scss',
    standalone: false
})
export class AgentProfileComponent {
	constructor(
		private readonly bottomSheet: MatBottomSheet,
		protected readonly auth: AuthService
	) {}

	@Input() agent: Agents;
	@Output() removeAgent = new EventEmitter();
	@Output() reInviteAgent = new EventEmitter();
	@Output() editPhoneNumber = new EventEmitter();
	@Output() viewAssignedAds = new EventEmitter();

	readonly isAgent = this.auth.user()?.roles.some((role) => role === 'isAgentUser');
	readonly isAdmin = +this.auth.user()?.hasBusinessProfile && !this.isAgent;

	defaultUserImg: string = env.defaultAvatarImg;
	readonly agentStatus = AgentStatus;
	readonly appRoute = APP_ROUTE;

	openBottomSheet(): void {
		const OPTIONS = [
			this.agent.status === this.agentStatus.ACCEPTED && {
				translationKey: 'n4059',
				textClass: 'border-bottom fg-general-5',
				action: AgentProfileAction.VIEW_ASSIGNED_ADS,
			},

			this.agent.status === this.agentStatus.ACCEPTED &&
				this.isAdmin && { translationKey: 'n4058', textClass: 'border-bottom fg-general-5', action: AgentProfileAction.EDIT_PHONE },

			this.agent.status === this.agentStatus.REJECTED &&
				this.isAdmin && { translationKey: 'n3802', textClass: 'border-bottom fg-general-5', action: AgentProfileAction.REINVITE },

			!+this.agent?.isAdmin &&
				this.isAdmin && {
					translationKey: this.agent.status === this.agentStatus.PENDING ? 'n4061' : 'n3511',
					textClass: 'fg-error-3 border-bottom',
					action: AgentProfileAction.REMOVE,
				},

			!+this.agent?.isAdmin &&
				this.agent?.userId === this.auth.user()?.id && {
					translationKey: 'n4057',
					textClass: 'fg-error-3 border-bottom',
					action: AgentProfileAction.REMOVE,
				},
		].filter(Boolean);

		this.bottomSheet
			.open(BottomsheetComponent, {
				data: { options: OPTIONS, title: 'n4060', headerClass: 'border-bottom', titleClass: 'text-body-regular' },
				panelClass: ['agent-action-menu'],
			})
			.afterDismissed()
			.subscribe((res) => {
				if (res) {
					switch (res.action) {
						case AgentProfileAction.EDIT_PHONE:
							this.editPhoneNumber.emit(this.agent);
							break;

						case AgentProfileAction.VIEW_ASSIGNED_ADS:
							this.viewAssignedAds.emit(this.agent);
							break;

						case AgentProfileAction.REINVITE:
							this.reInviteAgent.emit(this.agent.email);
							break;

						case AgentProfileAction.REMOVE:
							this.removeAgent.emit(this.agent);
							break;

						default:
					}
				}
			});
	}
}
