import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';

@Component({
    selector: 'findqo-cannot-remove-agent-dialog',
    templateUrl: './cannot-remove-agent-dialog.component.html',
    styleUrls: ['./cannot-remove-agent-dialog.component.scss'],
    standalone: false
})
export class CannotRemoveAgentDialogComponent {
	constructor(
		private dialogRef: MatDialogRef<CannotRemoveAgentDialogComponent>,
		private router: Router,
		@Inject(MAT_DIALOG_DATA) public data
	) {}

	readonly defaultImage = 'assets/images/profile/business-account-photo.svg';

	goToAds(): void {
		this.dialogRef.close();
		this.router.navigate([APP_ROUTE.agentHub.dashboard]);
	}

	close(): void {
		this.dialogRef.close();
	}
}
