import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AuthService } from '@core/data-access/services/auth.service';
import { Agents, BusinessProfile, ClientUser } from '@core/models';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';

@Component({
    selector: 'findqo-leave-agency-dialog',
    templateUrl: './leave-agency-dialog.component.html',
    styleUrl: './leave-agency-dialog.component.scss',
    standalone: false
})
export class LeaveAgencyDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public agent: Agents & { businessProfile: BusinessProfile },
		private readonly auth: AuthService,
		private readonly dialogRef: MatDialogRef<LeaveAgencyDialogComponent>,
		private readonly myProfileFacade: MyProfileFacade
	) {}

	user: ClientUser = this.auth.user();

	readonly defaultImage = 'assets/images/profile/business-account-photo.svg';

	removeAgent(): void {
		this.dialogRef.close();
		this.myProfileFacade.deleteAgent(this.agent);
	}

	cancel(): void {
		this.dialogRef.close();
	}
}
