import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { VALIDATOR } from '@core/constants';
import { AuthType } from '@core/enums';
import { ProfileInfoField } from '@core/enums/profile.enum';
import { AuthFacade } from '@feature/auth/auth.facade';
import { AuthDialogV2Component } from '@feature/auth/v2/auth-dialog-v2.component';

@Component({
    selector: 'findqo-edit-profile-info-dialog',
    templateUrl: './edit-profile-info-dialog.component.html',
    styleUrl: './edit-profile-info-dialog.component.scss',
    standalone: false
})
export class EditProfileInfoDialogComponent implements OnInit {
	constructor(
		@Inject(MAT_DIALOG_DATA) protected data,
		private readonly dialog: MatDialog,
		private readonly authFacade: AuthFacade,
		protected readonly dialogRef: MatDialogRef<EditProfileInfoDialogComponent>
	) {}

	readonly itemControl = new FormControl('', Validators.required);
	readonly profileInfoField = ProfileInfoField;

	updateEmail(): void {
		this.authFacade.changeEmailRequestOtp();
		this.dialogRef.close();

		this.dialog.open(AuthDialogV2Component, {
			data: { authType: AuthType.UPDATE_EMAIL_OTP, email: this.data.email },
			panelClass: 'auth-dialog',
			disableClose: true,
		});
	}

	ngOnInit(): void {
		switch (this.data?.target) {
			case ProfileInfoField.NAME:
				this.itemControl.addValidators([VALIDATOR.pattern.name, Validators.maxLength(80), Validators.minLength(3)]);
				break;

			case ProfileInfoField.PHONE:
				this.itemControl.addValidators([Validators.pattern('^[0-9]*$'), Validators.minLength(7), Validators.maxLength(10)]);
				break;

			default:
		}

		this.itemControl.patchValue(this.data[this.data.target]);
	}
}
