import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Agents } from '@core/models';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-remove-agent-dialog',
    templateUrl: './remove-agent-dialog.component.html',
    styleUrls: ['./remove-agent-dialog.component.scss'],
    standalone: false
})
export class RemoveAgentDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public agent: Agents,
		private dialogRef: MatDialogRef<RemoveAgentDialogComponent>,
		private myProfileFacade: MyProfileFacade
	) {}

	readonly buttonType = ButtonType;

	removeAgent(): void {
		this.dialogRef.close();
		this.myProfileFacade.deleteAgent(this.agent);
	}

	cancel(): void {
		this.dialogRef.close();
	}
}
