import { Component, Inject } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AppEmailValidator } from '@core/validators';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { ButtonType } from '@shared/components/button/button.component';
import { validate } from 'email-validator';

export interface DialogData {
	addAgents: boolean;
}

@Component({
    selector: 'findqo-add-agents-dialog',
    templateUrl: './add-agents-dialog.component.html',
    styleUrls: ['./add-agents-dialog.component.scss'],
    standalone: false
})
export class AddAgentsDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public data: DialogData,
		private dialogRef: MatDialogRef<AddAgentsDialogComponent>,
		private profileFacade: MyProfileFacade
	) {}

	readonly buttonType = ButtonType;

	agents = [];

	agentEmailControl = new FormControl('', [AppEmailValidator.validateEmailPattern()]);

	sendInvite(): void {
		this.onAddAgent();

		if (this.agents.length > 0) {
			this.dialogRef.close();
			this.profileFacade.addAgents(this.agents);
		} else {
			if (!this.agentEmailControl.value) {
				this.agentEmailControl.setErrors({ emailRequired: true });
			}
		}
	}

	onAddAgent(): void {
		this.agentEmailControl.markAsTouched();

		//if email pattern is correct remove all errors
		if (!this.agentEmailControl.hasError('emailPattern')) {
			this.agentEmailControl.setErrors(null);
		}
		if (!this.agentEmailControl.value) {
			this.agentEmailControl.setErrors({ emailRequired: true });
		}

		if (this.agentEmailControl.value && this.agentEmailControl.valid) {
			const EXISTING_AGENTS = this.profileFacade.getAgents();

			if (this.agentEmailControl.value.toLowerCase() === this.profileFacade.getUser()?.email) {
				this.agentEmailControl.setErrors({ emailPattern: true });
			} else {
				if (
					this.agents.findIndex((agent) => agent === this.agentEmailControl.value.toLowerCase()) === -1 &&
					EXISTING_AGENTS.findIndex((agent) => agent.email === this.agentEmailControl.value.toLowerCase()) === -1
				) {
					this.agents.push(this.agentEmailControl.value);
					this.agentEmailControl.reset();
					this.agentEmailControl.markAsUntouched();
				} else {
					this.agentEmailControl.setErrors({ existingEmail: true });
				}
			}
		}
	}

	deleteAgent(index: number): void {
		this.agents.splice(index, 1);
	}
}
